// 引入vue
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
Vue.use(VueRouter);
import vheader from 'component/mui/Header';

var router = new VueRouter();

//mui生命周期管理
mui.init({
    swipeBack: true
});


//头部head start
var HeaderVue = Vue.extend(vheader);
var header = new HeaderVue({
    propsData: {
        hdstyle: {
            back: {
                display: true
            },
            title: {
                display: true
            }
        },
        title: '编辑收货地址'
    },
    el: 'vheader'
});
//头部head end

//尾部foot start
var FootVue = Vue.extend({});
var footer = new FootVue({
    data: function() {
        return {
            butText: '保存并使用'
        }
    },
    el: "#bottomButton",
    methods: {
        saveAddress() {
            if (router.app) {
                router.app.saveAddress();
            }
        }
    }
});
//尾部 end

mui.plusReady(function() {
    if(!plus.webview.currentWebview().addressData) {
        header.title = '编辑收货地址';
    }
    if(plus.webview.currentWebview().fromContractSub) {
        footer.butText = '保存并使用';
    }
    router.start(app, '#app');
});
